import React from "react";
import { Link } from "react-router-dom";
import { TweenMax } from "gsap/all";
import { Container, Row, Col } from "react-bootstrap";
import { MainHeader } from "./HeaderStyled";
import { Heading1 } from "../../Styled/Typography";
import { useSelector, useDispatch } from "react-redux";
import { GlobalProjects, GlobalDataCount } from "../../Action/GlobalAction";

const Header = () => {
    const dispatch = useDispatch();
    const state = useSelector(state => state.projects);
    const complate = () => {
        state ? goBack() : projects();
        TweenMax.to(".pageOut", 1, { left: "100%", ease: "ease-in-out", delay: .3, onComplete: clear });
    }
    const pageOut = () => {
        TweenMax.to(".pageOut", 1, { visibility: "visible", right: 0, ease: "ease-in-out", onComplete: complate });
    };
    const clear = () => {
        TweenMax.to(".pageOut", 0, { visibility: "hidden", left: 0, right: null, ease: "ease-in-out" });
    }
    const projects = () => {
        dispatch(GlobalProjects(true));
    }
    const goBack = () => {
        dispatch(GlobalProjects(false));
        dispatch(GlobalDataCount(0));
    }
    return (
        <header className="p-4 p-sm-5">
            <div className="pageOut"></div>
            <Container fluid className="p-0">
                <Row noGutters>
                    <Col>
                        <MainHeader>
                            <Heading1>Soydan YILDIRIM</Heading1>
                            <Link to="" onClick={pageOut} className="font-type--1 font-weight--700">{ state ? "Back" : "Projects" }</Link>
                        </MainHeader>
                    </Col>
                </Row>
            </Container>
        </header >
    )
}

export default Header;