import React, { useEffect } from "react";
import { TweenMax } from "gsap/all";
import { CursorStyle, CursorWhiteDot } from "./CursorStyled";

const Cursor = () => {
    useEffect(() => {
        document.addEventListener("mousemove", CursorRun);
    }, []);
    const CursorRun = (e) => {
        let cursorX = e.clientX;
        let cursorY = e.clientY;
        let targetElement = e.target.tagName === "A" || e.target.tagName === "IMG" ? true : false;
    
        TweenMax.to("#cursor", .5, { left: cursorX, top: cursorY, scale: targetElement ? 4 : 1, ease: "ease-out", backgroundColor: targetElement ? "rgba(187, 134, 252, 1)" : "rgba(187, 134, 252, 1)" });
        TweenMax.to("#cursorDot", 0, { left: cursorX, top: cursorY });
    };

    return (
        <div>
            <CursorWhiteDot id="cursorDot"/>
            <CursorStyle id="cursor"/>
        </div>
    )
};

export default Cursor;