import React, { useEffect, useRef } from "react";
import Header from "../../Components/Header/Header";
import { HomeBackground, HomeContent, Confucius, DownloadCV } from "./HomeStyled";
import { FigureSafety, FigureLeft, FigureRight, ProjectArea, ProjectTextSafety, ProjectTitle, ProjectDescription, ProjectTextArea, ProjectLink } from "../Projects/ProjectsStyled";
import Typed from 'react-typed';
import { Heading2 } from "../../Styled/Typography";
import { TweenMax } from "gsap/all";
import { useSelector, useDispatch } from "react-redux";
import { GlobalDataCount } from "../../Action/GlobalAction";
import FooterComp from "../../Components/Footer/Footer";

const Home = () => {
    const state = useSelector(state => state)
    const dispatch = useDispatch();
    const figureSF = useRef();
    useEffect(() => {
        !state.projects && HomeAnim();
        if (document.querySelector("#figCapL") && document.querySelector("#figCapR")) {
            TweenMax.to("#figCapL", 1, { transform: "matrix(1, 0, 0, 1, 0, 0)", opacity: 1, delay: .6 });
            TweenMax.to("#figCapR", 1, { transform: "matrix(1, 0, 0, 1, 0, 50)", opacity: 1, delay: .8 });
        }
        if (document.querySelector("#projectName")) {
            TweenMax.to("#projectName", 1, { transform: "matrix(1, 0, 0, 1, 0, 0)", delay: .4 });
            TweenMax.to("#projectDescription", 1, { transform: "matrix(1, 0, 0, 1, 0, 0)", delay: .6 });
            TweenMax.to("#projectLink", 1, { opacity: 1, delay: .8 });
        }
    }, [state]);

    const HomeAnim = () => {
        if (document.querySelector("#confucius") && document.querySelector("#downloadCV")) {
            TweenMax.to("#confucius", .4, { delay: .5, y: 0, opacity: 1, ease: "ease-out" })
            TweenMax.to("#downloadCV", .4, { delay: .5, opacity: 1, ease: "ease-out" });
        }
    }

    const FigureAnimIn = () => {
        if (document.querySelector("#figCapL") && document.querySelector("#figCapR")) {
            TweenMax.to("#figCapL", .4, { transform: "matrix(1, 0, 0, 1, 0, 25)" });
            TweenMax.to("#figCapR", .4, { transform: "matrix(1, 0, 0, 1, 0, 25)" });
        }
    }
    const FigureAnimOut = () => {
        if (document.querySelector("#figCapL") && document.querySelector("#figCapR")) {
            TweenMax.to("#figCapL", .3, { transform: "matrix(1, 0, 0, 1, 0, 0)" });
            TweenMax.to("#figCapR", .3, { transform: "matrix(1, 0, 0, 1, 0, 50)" });
        }
    }
    const NextProject = (go) => {
        if (document.querySelector("#figCapL") && document.querySelector("#figCapR")) {
            TweenMax.to("#figCapL", .3, { transform: "matrix(1, 0, 0, 1, 0, 0)", opacity: 0 });
            TweenMax.to("#figCapR", .3, { transform: "matrix(1, 0, 0, 1, 0, 50)", opacity: 0 });
        }
        if (document.querySelector("#projectName")) {
            TweenMax.to("#projectName", 1, { transform: "matrix(1, 0, 0, 1, 0, 50)", ease: "ease-out", opacity: 0 });
            TweenMax.to("#projectDescription", 1, { transform: "matrix(1, 0, 0, 1, 0, 50)", ease: "ease-out", opacity: 0 });
            TweenMax.to("#projectLink", .3, { opacity: 0, onComplete: GlobalProjectCount(go) });
        }
    }
    const BeforeProject = (back) => {
        if (document.querySelector("#figCapL") && document.querySelector("#figCapR")) {
            TweenMax.to("#figCapL", .3, { transform: "matrix(1, 0, 0, 1, 0, 0)", opacity: 0 });
            TweenMax.to("#figCapR", .3, { transform: "matrix(1, 0, 0, 1, 0, 50)", opacity: 0 });
        }
        if (document.querySelector("#projectName")) {
            TweenMax.to("#projectName", 1, { transform: "matrix(1, 0, 0, 1, 0, 50)", ease: "ease-out", opacity: 0 });
            TweenMax.to("#projectDescription", 1, { transform: "matrix(1, 0, 0, 1, 0, 50)", ease: "ease-out", opacity: 0 });
            TweenMax.to("#projectLink", .3, { opacity: 0, onComplete: GlobalProjectCount(back) });
        }
    }
    const GlobalProjectCount = (WTF) => {
        if(WTF === 1) {
            state.dataCount === state.data.length - 1 ? dispatch(GlobalDataCount(0)) : dispatch(GlobalDataCount(state.dataCount = state.dataCount + 1))
        } else {
            state.dataCount === 0 ? dispatch(GlobalDataCount(state.data.length - 1)) : dispatch(GlobalDataCount(state.dataCount = state.dataCount - 1))
        }
    }
    const nextOrBefore = e => {
        const FS = figureSF.current.offsetWidth;
        const mousePos = e.clientX - figureSF.current.getBoundingClientRect().left;
        mousePos < FS / 2 ? BeforeProject(0) : NextProject(1)
    }

    return (
        <HomeBackground>
            <Header />
            {
                state.projects ? state.data.map((item, index) =>
                    index === state.dataCount && (
                        <ProjectArea key={index}>
                            <FigureSafety ref={figureSF} onClick={nextOrBefore} onMouseLeave={FigureAnimOut}>
                                <FigureLeft id="figCapL" onMouseOver={FigureAnimIn}>
                                    <img className="img-fluid" src={item.imageLeftUrl} alt="" />
                                </FigureLeft>
                                <FigureRight id="figCapR" onMouseOver={FigureAnimIn}>
                                    <img className="img-fluid" src={item.imageRightUrl} alt="" />
                                </FigureRight>
                            </FigureSafety>
                            <ProjectTextSafety>
                                <ProjectTextArea>
                                    <ProjectTitle id="projectName">{item.name}</ProjectTitle>
                                </ProjectTextArea>
                                <ProjectTextArea>
                                    <ProjectDescription id="projectDescription">{item.description}</ProjectDescription>
                                </ProjectTextArea>
                                <ProjectTextArea>
                                    <ProjectLink href={item.link} target="blank" id="projectLink">{item.link}</ProjectLink>
                                </ProjectTextArea>
                            </ProjectTextSafety>
                        </ProjectArea>
                    )
                ) : <HomeContent>
                        <div>
                            <Heading2>
                                I'm <Typed strings={['a Front-End Developer.^2000', 'an Animator.^2000', 'a Gamer.^2000', 'a Human :)^2000']} typeSpeed={70} backSpeed={10} loop />
                            </Heading2>
                            <Confucius id="confucius">
                                If somewhere little people have big shadows, it means that the sun is going down.
                    </Confucius>
                            <DownloadCV id="downloadCV" href={require("../../Cv/Soydan_YILDIRIM.pdf")} download>Download CV</DownloadCV>
                        </div>
                    </HomeContent>
            }
            <FooterComp/>
        </HomeBackground>
    )
}

export default Home;