const GlobalState = (state = initialState.Globalstate, action) => {
    switch (action.type) {
        case "PROJECTS":
            return {
                ...state,
                projects: action.value
            }
        case "DATA_COUNT":
            return {
                ...state,
                dataCount: action.count
            }
        default:
            return state
    }
}

export default GlobalState;
